.arrow{
    padding-top: 30px;
    padding-right: 20px;
    color: #8FC5B5;
    }

.titleCar{
        font-weight: bolder;
        color:#4BCAA4;
      }
      
      .infoCar{
        font-weight: bolder;
        padding-bottom: 10px;
      }

    .form-group{
    width: 130px;
    }

    #telNumber
    {
    padding-left: 42px !important;
    position: relative;
    }

    .next{
        border:white 3px solid;
        width: 100%;
        padding: 10px;
        border-radius: 10px;
        color: white;
        font-size: 25px;
        font-weight: bold;
        background-color: #4BCAA4;
        }
        .next:hover{
            border: #4f5967;
            color: white;
        }
        .btn-next{
        position: absolute;
        bottom: 30px;
        padding:80px 20px;
        padding-bottom: 0;
        width: 100%;
        text-decoration: none;
        }


